.wrapper {
    max-width: 90%;
    margin: 0 auto;
    position: relative;
}

@media only screen and (min-width: $bp-desktop) {
    .wrapper {
        max-width: 960px;
    }
}

@media only screen and (min-width: $bp-desktop-large) {
    .wrapper {
        max-width: 1100px; 
    }
}